@import '_variables.scss';
html {
  -ms-overflow-style: scrollbar;
}
html{
  font-family: $default-font-family;
}
body {
  color: $main-color;
  font-family: $default-font-family;
  font-size: $fontSize;
  line-height: $fontLineHeight;
  margin: 0;
  padding: 0;
  @include rwd(laptop-s){
    font-size: $fontSize - 1;
  }
}
a {
  color: $link-color;
  outline: none;
  text-decoration: none;
  &:hover, &:focus {
    text-decoration: none !important;
    outline: none !important;
    color: $hover-color;
  }
  &:focus {
    -moz-outline-style: none;
    outline: none;
    color: inherit;
  } 
  &:hover:focus {
      text-decoration: none !important;
      outline: none !important;
  }
  img {
    border: none;
  }
}
input {
  outline: none;
}
img {
  max-width: 100%;
}
// bootstrap
.no-gutters {
  margin-right: 0;
  margin-left: 0;
  >.col,
  >[class*="col-"] {
    padding-right: 0;
    padding-left: 0;
  }
}
.wrap {
  width: $container-size;
  margin: auto;
  padding: 0 30px;
  @include rwd-custom($container-size){
    width: 100%;
  }
  // @include rwd-custom(1400px) {
  //   width: 100%;
  //   padding: 0 30px;
  // }
  @include rwd(tablet-l) {
    padding: 0 20px;
  }
}
.container {
  padding-left: 0;
  padding-right: 0;
  @media (min-width: 768px) {
    width: auto;
  }
}


.sb-active .header--clone {
