//alert 

.alert-info, 
.alert-danger{
  padding: 20px 20px;
  margin: 20px 0;
  border-radius: 0;
  @include rwd(tablet){
    padding: 10px;
    margin-top: 15px;
  }
  > *:last-child{
    margin-bottom: 0;
  }
}
.alert-info{  
  border: 1px solid $font-color-light1;  
  background: lighten($color-gray2, 49.5%);
  color: $active-color;  
  hr{
    border-top-color: $font-color-light1
  }
}

.alert-danger{
  background: rgba($btn-danger-link-color, .24);
  color: $btn-danger-link-color;  
}

.alert_collapse{
  overflow: hidden;
  position: relative;

  .alert_btn-wrap{
    display: none;
    position: absolute;
    bottom: 0px;
    left: 0;
    width: 100%;
    padding: 20px 0;
    text-align: center;
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#f5f5f5+0,f5f5f5+100&0+0,0.77+31,1+50 */
    background: -moz-linear-gradient(top,  rgba(245,245,245,0) 0%, rgba(245,245,245,0.77) 31%, rgba(245,245,245,1) 50%, rgba(245,245,245,1) 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(top,  rgba(245,245,245,0) 0%,rgba(245,245,245,0.77) 31%,rgba(245,245,245,1) 50%,rgba(245,245,245,1) 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom,  rgba(245,245,245,0) 0%,rgba(245,245,245,0.77) 31%,rgba(245,245,245,1) 50%,rgba(245,245,245,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00f5f5f5', endColorstr='#f5f5f5',GradientType=0 ); /* IE6-9 */


    .btn{
      min-width: 20%;
      margin: 0 auto;
      border-radius: 20px;
    }
  }

  &.alert_view-part{
    max-height: 310px; 
    padding-bottom: 70px; 
    .close-collapse{
      display: none;
    }
    .open-collapse{
      display: block;
    }

    .alert_btn-wrap{
      display: block;
    }

    &.open{
      max-height: none;

      .close-collapse{
        display: block;
      }
      .open-collapse{
        display: none;
      }
    }
  }
}

