/// 表單元件
.form-group--nomargin{
  margin-bottom: 0;
}
.form-control{
  height: 38px;
  font-size: 15px;
  border-radius: 0;
  box-shadow: none;
  border-color: #C5C7D3;
}

.form-inline{
  .form-group .form-control{
    vertical-align: baseline;
  }
  > label{
    // margin-top: 5px;
  }
}

.input-group{
  border:1px solid $font-color-light1;
  background: #fff;
  .form-control{
    border: 0;
    height: 36px;
  }
}
.help-block{
  color: $btn-danger-link-color;
  font-size: 14px;
  .big{
    font-size: 1.4em;
    font-weight: bold;
  }
}
.help-block2{
  color: $btn-default-hover-color;
}
.input-group.disabled{
  .form-control{
    cursor: not-allowed;
    background-color: #eee;
  }
  .input-group-addon{
    background-color: #eee;
    color: lighten($main-color, 20);
  }
}
.input-group-addon{
  border-radius: 0;
  border: 0;
  background: lighten($font-color-light3, 0.8);
}

.input-group_style2{
  .input-group-addon{
    background: transparent;
    color: $font-color-dark3;
  }
}
fieldset[disabled] .input-group-addon{
  background-color: #eee;
  color: lighten($main-color, 20);
}

.checkbox{
  .form-control-label{
    font-size: 17px;
  }
  &.checkbox_inline{
    display: inline-block;
    margin-right: 20px;
    margin-top: 0;
  }
  &.checkbox_no-margin{
    margin: 0;
  }
}

.radio{
  .help-block{
    margin-left: 20px;
    margin-top: 0;
  }
}
.form-group{
  label + .radio{
    margin-top: 0;
  }
}
input[type=checkbox], input[type=radio]{
  margin-top: 7px;
}
.checkbox input[type=checkbox], .checkbox-inline input[type=checkbox], .radio input[type=radio], .radio-inline input[type=radio]{
  margin-top: 8px\9;
}

.checkbox_follow-controls{
  margin-top: -10px;
  margin-left: 32px;
  font-size: 15px;
  margin-bottom: 10px;
  .form-inline + .form-inline{
    margin-top: 10px;
  }
}


/* 錯誤提示 */
.has-error .form-control{
  border-color: $btn-danger-link-color;
}
.has-danger .with-errors {
  color: $btn-danger-link-color;
}

.has-danger input {
  border: solid 1px $btn-danger-link-color;
}
.has-error .form-control:focus{
  border-color: $btn-danger-hover-color;
  -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 6px $btn-danger-disabled-color;
  box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 6px $btn-danger-disabled-color;
}
.has-error .checkbox, .has-error .checkbox-inline, .has-error .control-label, .has-error .help-block, .has-error .radio, .has-error .radio-inline, .has-error.checkbox label, .has-error.checkbox-inline label, .has-error.radio label, .has-error.radio-inline label{
  color: $btn-danger-link-color;
}

.help-block.with-errors li:before {
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  // content: url(../images/error.png);
  font-size: 18px;
  content: "\f05a";
  padding: 0 5px 0 0;
  vertical-align: middle;
}

