// right
.right-logo {
  text-align: center;
  padding: 13px 0;
  border-bottom: 3px solid $color-primary;
  float: none;
  width: 100%;
  display: block;
  img {
    width: 135px;
    height: 30px;
  }
}
.right-menu, .right-lang {
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    li {
      a {
        display: block;
        text-align: left;
        font-size: $fontSize;
      }
    }
    >li {
      border-bottom: 1px solid $color-primary;
    }
  }
  .dropdown-menu {
    position: static;
    float: none;
    box-shadow: none;
    border-radius: 0;
    border-bottom: none;
    border-color: $color-white-d10;
    padding: 0;
    margin: 0;
    a {
      padding: 4px 6px 4px 16px;
      border-bottom: 1px solid $color-white-d10;
      @include font-awesome-icon(before, '\f101', $color-primary);
      &::before {
        margin-right: 4px;
      }
    }
  }
  @include rwd-custom(991px) {
    .dropdown:hover .dropdown-menu a, .dropdown:hover .dropdown-menu a.active {
      padding: 6px 12px;
    }
    .dropdown:hover .dropdown-menu a:hover, .dropdown:hover .dropdown-menu a:focus, .dropdown:hover .dropdown-menu a.active, .dropdown:hover .dropdown-menu a.active {
      &::before {
        color: $color-white;
      }
    }
  }
}
.btn.active, .btn:active {
  box-shadow: none;
}
.right-title {
  background-color: $color-primary;
  color: $color-white;
  padding: 2px 12px;
  font-size: $fontSize-12;
  @include font-awesome-icon(before, '\f110', $color-white);
  &::before {
    margin-right: 4px;
  }
}
.right-lang {
  .boot-link {
    a {
      display: block;
      width: 100%;
      border-radius: 0;
      padding: 6px 12px;
      &:focus {
        outline: none;
        text-decoration: none;
      }
    }
  }
  .lang-link {
    >img {
      display: none;
    }
  }
}
@include rwd-custom(991px) {
  .dropdown-backdrop {
    display: none;
  }
}
