.footer {
  background-color: $color-black-l30;
  color: $color-gray2-l25;
  padding: 36px 0 30px;
  @include rwd(tablet-s){
    padding: 24px 0 20px;
  }
  a {
    transition: all 0.4s ease;
    color: $color-gray2-l25;
    &:hover {
      color: $color-white;
      text-decoration: underline;
    }
  }
  .wrap {
    position: relative;
  }
  .left {
    float: left;
    width: 50%;
  }
  .right {
    float: right;
    width: 50%;
  }
  @include rwd(tablet-l) {
    .left, .right {
      @include no-flot;
    }
  }
}
// .goto-top {
//   position: absolute;
//   right: 0;
//   top: 0;
//   cursor: pointer;
//   img {
//     width: 24px;
//     height: 24px;
//   }
// }
.foot, .foot-down {
  @include clearfix;
}
.foot {
  .right {
    width: 29%;
    margin-top: 72px;
  }
  .download {
    img {
      width: 92px;
      height: 92px;
      vertical-align: text-top;
      margin-left: 24px;
    }
  }
  @include rwd(tablet-l) {
    .right {
      width: 100%;
      margin-top: 0;
    }
  }
}
.foot-info {
  .organ {
    margin-bottom: 24px;
    span {
      &::before {
        content: "";
        width: 1px;
        background: $color-white;
        height: 23px;
        display: inline-block;
        margin: 0 12px;
      }
    }
    a {
      position: relative;
      &::after {
        content: "";
        background: url(../images/tmba-w.png) no-repeat;
        display: inline-block;
        width: 37px;
        height: 43px;
        transition: all 0.3s ease;
        margin-left: 4px;
      }
      &:hover::after {
        background: url(../images/tmba.png) no-repeat;
      }
    }
    img {
      vertical-align: baseline;
    }
  }
  .title {
    color: $color-white;
  }
}
.follw {
  margin-bottom: 12px;
  .icon {
    margin-left: 86px;
    a {
      background: $color-white-d30;
      display: inline-block;
      width: 30px;
      height: 21px;
      line-height: 21px;
      text-align: center;
      border-radius: 3px;
      transition: all 0.4 ease;
      &.fb {
        margin-right: 10px;
        img {
          width: 7px;
          height: 14px;
          margin-top: -2px;
        }
        &:hover {
          background: $facebook;
        }
      }
      &.youtube {
        img {
          width: 9px;
          height: 11px;
          margin-top: -3px;
        }
        &:hover {
          background: $youtube;
        }
      }
    }
  }
}
.foot-down {
  border-top: 1px solid $color-gray2-l25;
  margin-top: 30px;
  padding-top: 12px;
  .right {
    width: inherit;
    position: relative;
    @include clearfix;
  }
}
.browser {
  img {
    width: 18px;
    height: 18px;
    margin-top: -4px;
  }
  
}
.privacy {
  float: left;
}
.design {
  float: right;
  a {
    color: $color-white;
  }
}

// new footer style
.footer-section1{
  @include clearfix;
}
.footer-info{
  float: left;
  width: 70%;
  @include rwd(tablet-s){
    float: none;
    width: auto;
  }
}
.footer-social-wrap{
  float: right;
  width: 30%;
  text-align: right;
  margin-top: 20px;
  @include rwd(tablet-s){
    float: none;
    width: auto;
    text-align: left;
  }
}
.footer-info_logo-wrap{
  margin-bottom: 15px;
  > div{
    display: inline-block;    
    vertical-align: bottom;
  }
}
.footer-info_logo-tmts{
  margin-right: 12px;
  @include rwd(mobile-l){
    margin-right: 10px;
  }
  img{
    height: 20px;
    @include rwd(mobile-l){
      height: 18px;
    }
  }
}
.footer-info_logo-orga{
  color: #fff;
 > *{
   display: inline-block;
   vertical-align: text-bottom;
 }
 span{
   line-height: 1;
   padding-right: 6px;
 }
 a{
  img:last-child{
    display: none;
  }
  &:hover{
    img:first-child{
      display: none;
    }
    img:last-child{
      display: inline-block;
    }
  }
 }
}
.footer-info_main{
  .footer-info_main-title{
    font-size: 19px;
    font-weight: 600;
    color: #fff;
    @include rwd(tablet-s){
      font-size: 17px;
    }
    @include rwd(mobile-l){
      font-size: 16px;
    }
  }
  ul{
    @include clearul;
    @include rwd(mobile-l){
      font-size: 15px;
    }
    li{
      line-height: 1.7em;
    }
  }
}
.lang-en{
  .footer-info_main .footer-info_main-title{
    font-weight: 500;
  }
}
.footer-qrcode{
  background: #fff;
  display: inline-block;
  padding: 15px 20px 10px;
  font-size: 15px;
  font-weight: 500;
  text-align: center;
  @include rwd(tablet-l){
    padding: 12px 16px 6px;
  }
  @include rwd(tablet-s){
    padding: 8px 10px 2px;
  }
  a img{
    width: 94px;
    height: 94px;
  }
  span{
    display: block;
    color: $font-color-dark1;
    @include rwd(tablet-l){
      font-size: 14px;
    }
  }
  span i{
    font-size: 19px;
    @include rwd(tablet-l){
      font-size: 17px;
    }
  }
}

.footer-smo{
  @include rwd(tablet-s){
    margin-top: 12px;
  }
  a{
    font-size: 22px;
    padding: 0 2px;
    margin-left: 8px;
    color: $font-color-light3;
    &.smo-fb:hover{
      color: #4267B2;
    }
    &.smo-youtube:hover{
      color: #ff0000;
    }
  }
}
.footer-section2{
  border-top: 1px solid #707070;
  margin-top: 20px;
  padding-top: 10px;
  @include clearfix;
  font-size: 14px;
  font-weight: 300;
  line-height: 1.5;
  @include rwd(tablet-s){
    margin-top: 12px;
  }
  
}

.footer-copyright-wrap{
  float: left;
  @include rwd(tablet-xl){
    float: none;
  }
}
.footer-copyright{
  a{
    color: #fff;
    &:hover{
      color: $hover-color;
    }
  }
}
.footer-browser-support{
  @include rwd(tablet-s){
    margin-top: 8px;
  }
}
.footer-designby{
  float: right;
  @include rwd(tablet-xl){
    float: none;
    margin-top: 6px;
  }
  // vertical-align: bottom;
  a{
    display: inline-block;
    vertical-align: text-bottom;
    margin-left: 6px;
  }
  img{
    height: 20px;
    @include rwd(tablet-s){
      height: 16px;
    }
  }
}

.sending-wrap {
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  border-radius: 6px;
  width: 100px;
  height: 54px;
  background: #ddd;
  margin: 0 auto 8px;
  position: relative;

  .la-ball-fall.sending {
    opacity: 1;
    visibility: visible;
    margin-top: -9px;
  }
  .la-ball-fall{
    color: #CCC;
  }
}


.goto-top, .quick, .favorite_rfix{
  display: none;
}

.quickmenu-wrap{
  position: fixed;
  right: 60px;
  transform: translateX(100%);
  bottom: 20px;
  z-index: 999;
  line-height: 1.4;
  // &.active-b {
  //   bottom: 20px;
  // }
  @include rwd(laptop-xl){
    right: 40px;
    bottom: 35%;
    transform:  translateX(100%) translateY(50%);
  }
  @include rwd(tablet){
    bottom: 50%;
  }
  .wrap {
    width:  auto;  
    text-align: right;
    // transform: translateY(100%);  
    transition: all 300ms ease-in-out;
    // opacity: 0;
    padding: 0;
  }
  // &.active .wrap {
	// 	opacity: 1;
	// 	-webkit-transform: translateY(0%);
	// 	-ms-transform: translateY(0%);
	// 	-o-transform: translateY(0%);
	// 	transform: translateY(0%);
		
	// }

  i.fa{
    font-size: 22px;
    line-height: 38px;
  }
  //.quickmenu-search,
  .quickmenu-favorite,  
  .quickmenu-top{
    margin-top: 15px;
    @include rwd(laptop-xl){
      margin-top: 0px;
    }
  }
  .quickmenu-favorite,
  .quickmenu-top{
    a{
      width: 40px;
      height: 40px;
      display: block;
      text-align: center;
    }
  }
  .quickmenu-search{
    //text-align: right;
    
    a{
      background: $hover-color2;
      color: #fff;       
      height: 40px;
      overflow: hidden;   
      display: block; 
      padding: 0 10px;
      transform: rotate(-90deg)  translateX(0) translateY(100%);
      transform-origin: left bottom;
      cursor: pointer;
      &:hover{
        background: $link-color2;
      }
    }
    .quickmenu-search_icon{
      transform: rotate(90deg);
    }
    .quickmenu-search_text{
      padding-right: 10px;
      display: inline-block;
      @include rwd(tablet){
        display: none;
      }
    }
  }
  .quickmenu-favorite{
    @include rwd(tablet){
      display: none;
    }
    a{      
      background: #fff;
      border: 1px $font-color-dark1 solid;
      color: $font-color-dark6;
      position: relative;
      @include rwd(laptop-xl){
        background: $font-color-dark6;
        border-color: $font-color-dark6;
        color: #fff;
      }
      &:hover{
        background: $font-color-dark6;
        border-color: $font-color-dark6;
        color: #fff;
        @include rwd(laptop-xl){
          background: darken($color: $font-color-dark6, $amount: 5);
          border-color: darken($color: $font-color-dark6, $amount: 5);
        }
      }
    }
    .quickmenu-favorite_num{
      top: -12px;
      left: -12px;
      background: lighten($color: $font-color-dark6, $amount: 10);
      color: #fff;
      font-size: 13px;
      width: 25px;
      height: 25px;
      border-radius: 50%;
      position: absolute;
      line-height: 25px;
      text-align: center;
      @include rwd(laptop-xl){
        top: 6px;
        left: auto;
        right: 100%;
        margin-right: -5px;        
       
      }
    }
    a:hover .quickmenu-favorite_num{
      background: #fff;
      border:1px lighten($color: $font-color-dark6, $amount: 10) solid;
      color: lighten($color: $font-color-dark6, $amount: 10);
      transition: none;
    }
  }
  
  .quickmenu-top{
    display: none;
    &.noactive{
      // opacity: 0;
      // height: 0;
      // display: none;
    }
    a{      
      background: #fff;
      border: 1px $font-color-dark1 solid;
      color: $font-color-dark1;   
      @include rwd(laptop-xl){
        background: $font-color-dark1;
        border-color: $font-color-dark1;
        color: #fff;
      }  
      &:hover{
        background: $font-color-dark2;
        color: #fff;
      } 
    }
  }
}