// clearfix
@mixin clearfix {
  &::after {
    content: "";
    display: table;
    clear: both;
  }
}
@mixin clearul {
  list-style: none;
  margin: 0;
  padding: 0;
}
// center(flex)
@mixin flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

// float none
@mixin no-flot {
  float: none;
  width: 100%;
}

//icon png(ie8) svg
@mixin svg-icon($icon, $widit, $height) {
  display: inline-block;
  background-image: url("../images/#{$icon}.png");
  background-image: none, url("../images/#{$icon}.svg");
  width: $width;
  height: $height;
}

@mixin transition{
	-webkit-transition: all 0.2s ease;
	-moz-transition: all 0.2s ease;
	-ms-transition: all 0.2s ease;
	-o-transition: all 0.2s ease;
	transition: all 0.2s ease;
}